import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSearch, CsxMultipleSelect} from 'Form'
import {
  API_USER_WAREHOUSELIST,
  API_PRODUCT_CATEGORYSONS,
  API_PRODUCTS_KEYVALUES,
  API_STOCKRELOCATION_SHIPPERKEYVALUES,
  API_STOCKRELOCATION_RESERVOIRKEYVALUES,
  API_STORAGELOCATIONS_GETSTORAGELOCATIONFUZZYKEYVALUES,
  API_PRODUCT_GETPRODUCTKEYVALUES,
  API_DICTIONARYS,
  API_PRODUCT_GETPRODUCTPURCHASEGROUPS,
  API_PRODUCT_GETPRODUCTSPLITGROUPS_KEYVALUE
} from 'api'

@connect(
  state => ({...state.stockQuery, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size, userLoation} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }
  // 重置
  reset = () => {
    const {update, userLoation} = this.props
    update({searchParams: {warehouseCode: {value: userLoation.value, children: userLoation.value}}})
  }
  render() {
    const {
      searchParams: {
        productCode,
        warehouseCode,
        reservoirAreaCode,
        reservoirAreaTypeCode,
        shipperCode,
        storeLocationCode,
        storageLocationTypeCode,
        bigCategoryCode,
        centreCategoryCode,
        smallCategoryCode,
        productName,
        purchaseGroupCode,
        splitGroupCode
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='仓库'
              selectItem={{data: warehouseCode ? warehouseCode : {value: userLoation.value, children: userLoation.value}, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: warehouseCode ? warehouseCode.value : userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_STOCKRELOCATION_RESERVOIRKEYVALUES}
              label='库区'
              searchParamsKey='reservoirAreaCode '
              searchParams={{warehouseCode: warehouseCode ? warehouseCode.value : userLoation.value}}
              selectItem={{data: reservoirAreaCode, key: 'key', valueKey: ['key', 'value']}}
              onSelect={(value) => {
                this.onInputChange({reservoirAreaCode: value})
              }}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              label='库区类型'
              dateApi={API_DICTIONARYS}
              searchParams={{dicTypes: 'DATYPE'}}
              selectItem={{data: reservoirAreaTypeCode, key: 'dicKey', valueKey: 'dicValue'}}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaTypeCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
              label='货主名称'
              selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shipperCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_STORAGELOCATIONS_GETSTORAGELOCATIONFUZZYKEYVALUES}
              label='储位'
              searchParamsKey='storageLocationCode'
              searchParams={{warehouseCode: warehouseCode ? warehouseCode.value : userLoation.value}}
              selectItem={{data: storeLocationCode, key: 'key', valueKey: ['key', 'value']}}
              onSelect={(value) => {
                this.onInputChange({storeLocationCode: value})
              }}
              onChangeSelect={(value) => {
                this.onInputChange({storeLocationCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              label='储位类型'
              dateApi={API_DICTIONARYS}
              searchParams={{dicTypes: 'STORAGEYPE'}}
              selectItem={{data: storageLocationTypeCode, key: 'dicKey', valueKey: 'dicValue'}}
              onChangeSelect={(value) => {
                this.onInputChange({storageLocationTypeCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_PRODUCT_GETPRODUCTPURCHASEGROUPS}
              label='采购组'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: purchaseGroupCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({purchaseGroupCode: value})
              }}
            />
          </Col>
          <Col span={16}>
            <Row type="flex" justify="start">
              <CsxMultipleSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label='商品分类'
                searchParams={{type: '2'}}
                selectItem={{data: bigCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({bigCategoryCode: value})
                }}
                style={{width: 163}}
              />
              <CsxMultipleSelect
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: bigCategoryCode && bigCategoryCode.value, level: '3'}}
                selectItem={{data: centreCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({centreCategoryCode: value})
                }}
                noReady
                errMessage = '请先选择大类'
                style={{width: 163, marginLeft: 10}}
              />
              <CsxMultipleSelect
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: centreCategoryCode && centreCategoryCode.value, level: '4'}}
                selectItem={{data: smallCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({smallCategoryCode: value})
                }}
                noReady
                errMessage = '请先选择中类'
                style={{width: 163, marginLeft: 10}}
              />
            </Row>
          </Col>

          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_PRODUCT_GETPRODUCTSPLITGROUPS_KEYVALUE}
              label='拆单分组'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: splitGroupCode, key: 'value', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({splitGroupCode: value})
              }}
            />
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



