import * as actionType from './actionType'
import {getData} from 'util'
import {API_PRODUCTSTOCK_LOADLIST_PAGE, API_PRODUCTSTOCK_EXPORTPRODUCTSTOCK_EXPORT, API_PRODUCTSTOCK_EXPORTPRODUCTSTOCKDETAIL_EXPORT, API_PRODUCTSTOCK_ORDERLIST, API_PRODUCTSTOCK_ORDERLIST_EXPORT, API_GET_MENUS_PERMISSIONS_BANS} from 'api'
//同步
export const update = (object) => ({
  type: actionType.STOCKQUERY_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  // if (!reqData.userLoation || !reqData.userLoation.value ) {
  //   return
  // }
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      // reqObj[i] = reqObj[i].value
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
    }
  }
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  dispatch({type: actionType.STOCKQUERY_UPDATE, data: {tableLoading: true}})
  getData({api: API_PRODUCTSTOCK_LOADLIST_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.STOCKQUERY_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOCKQUERY_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_PRODUCTSTOCK_EXPORTPRODUCTSTOCK_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOCKQUERY_FILE
    })
  })
}

// 明细导出
export const handleExportDet = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_PRODUCTSTOCK_EXPORTPRODUCTSTOCKDETAIL_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOCKQUERY_FILE
    })
  })
}

// 获取在途库存查询列表
export const handleGetListOM = (reqData) => (dispatch) => {
  dispatch({type: actionType.STOCKQUERY_UPDATE, data: {deliveryTableLoading: true}})
  getData({api: API_PRODUCTSTOCK_ORDERLIST, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.STOCKQUERY_UPDATE,
      data: {
        deliveryTableLoading: false,
        totalOM: data.result.total,
        pageOM: reqData.searchParams.page,
        sizeOM: reqData.searchParams.size,
        searchResultRecord: data.result.records || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOCKQUERY_FILE
    })
  })
}

// 导出
export const handleExportOM = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.STOCKQUERY_UPDATE, data: {modalTuttonLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_PRODUCTSTOCK_ORDERLIST_EXPORT, ...reqData}).then(data => {
    dispatch({
      type: actionType.STOCKQUERY_UPDATE,
      data: {
        modalTuttonLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.STOCKQUERY_FILE
    })
  })
}
// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    console.log(data)
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.STOCKQUERY_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.STOCKQUERY_FILE
    })
  })
}



