import React, {PureComponent} from 'react'
import {
  Table,
  Icon,
  Popover
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'

@connect(
  state => ({...state.stockQuery, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '仓库',
      width: 150,
      dataIndex: 'warehouseCode',
      key: 'warehouseCode'
    }, {
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode',
      render: (text, record, index) => <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '商品条码',
      width: 150,
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      render: (text, record, index) => <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{width: '205px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '货主',
      width: 100,
      dataIndex: 'shipperName',
      key: 'shipperName'
    }, {
      title: '拣选面',
      width: 150,
      dataIndex: 'pickingSide',
      key: 'pickingSide',
      render: (text, record, index) => <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '库存总量',
      width: 100,
      dataIndex: 'qty',
      key: 'qty'
    }, {
      title: '预占量',
      width: 150,
      dataIndex: 'preOccupyQty',
      key: 'preOccupyQty'
    }, {
      title: '占用量',
      width: 100,
      dataIndex: 'occupyQty',
      key: 'occupyQty'
    }, {
      title: '可用量',
      width: 100,
      dataIndex: 'availableQty',
      key: 'availableQty'
    }, {
      title: '待收货量',
      width: 100,
      dataIndex: 'waitForReceiveQty',
      key: 'waitForReceiveQty'
    }, {
      title: '途损区',
      width: 100,
      dataIndex: 'roadDamageArea',
      key: 'roadDamageArea',
      render: (text, record, index) => <a onClick={() => {
        const {update} = this.props
        update({
          isShowQueryModal: true,
          queryModalId: record.productCode,
          queryModalTotalNum: text
        })
      }}>{text}</a>
    }, {
      title: '退货区',
      width: 100,
      dataIndex: 'returnProductArea',
      key: 'returnProductArea'
    }, {
      title: '盘点待处理区',
      width: 150,
      dataIndex: 'checkPendingArea',
      key: 'checkPendingArea'
    }]

    this.columns2 = [{
      title: '储位',
      width: 150,
      dataIndex: 'storeLocationCode',
      key: 'storeLocationCode',
      render: (text, record, index) => <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '储位类型',
      width: 150,
      dataIndex: 'storageLocationTypeName',
      key: 'storageLocationTypeName'
    }, {
      title: '库区编号',
      width: 150,
      dataIndex: 'reservoirAreaCode',
      key: 'reservoirAreaCode'
    }, {
      title: '库区名称',
      width: 150,
      dataIndex: 'reservoirAreaName',
      key: 'reservoirAreaName'
    }, {
      title: '库区类型',
      width: 150,
      dataIndex: 'reservoirAreaTypeName',
      key: 'reservoirAreaTypeName'
    }, {
      title: '库存量',
      width: 150,
      dataIndex: 'qty',
      key: 'qty'
    }, {
      title: '占用量',
      width: 100,
      dataIndex: 'occupyQty',
      key: 'occupyQty'
    }, {
      title: '可用量',
      width: 150,
      dataIndex: 'availableQty',
      key: 'availableQty'
    }]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    handleGetList({searchParams: {page, size, ...reqObj}, userLoation})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}, userLoation: newProps.userLoation})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...reqObj, isAsc: true, columns: sorter.field}, userLoation})
      } else {
        handleGetList({searchParams: {page, size, ...reqObj, isAsc: false, columns: sorter.field}, userLoation})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...reqObj}, userLoation})
    }
  }
    // 打印条形码
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          expandedRowRender={record => record.detailList && record.detailList.length ? <Table
            rowKey={(record, index) => index}
            columns={this.columns2}
            dataSource={record.detailList}
            scroll={{x: 'max-content'}}
            pagination={false}
          /> : <p>暂无数据</p>}
        />
      </div>
    )
  }
}



